import React from 'react'
import { Link, withRouter } from 'react-router-dom'

class Navbar extends React.Component {
  constructor(){
    super()
    this.state = { active: false}
    this.toggleActive = this.toggleActive.bind(this)
  }

  toggleActive() {
    this.setState({ active: !this.state.active })
  }

  componentDidUpdate(prevProps) {
    if(prevProps.location.pathname !== this.props.location.pathname) {
      this.setState({ active: false })
    }
  }


  render() {
    return (
      <nav className="navbar">
        <div className=" columns is-centered">
          <div className="column is-two-thirds-desktop">
            <a role="button" className={`navbar-burger${this.state.active ? ' is-active' : ''}`} onClick={this.toggleActive}>
              <span aria-hidden="true"></span>
              <span aria-hidden="true"></span>
              <span aria-hidden="true"></span>
            </a>


            <div className={`navbar-menu${this.state.active ? ' is-active' : ''}`}>
              <div className="navbar">
                <Link to="/about" className="navbar-item">
                  <p className="title is-5">about.</p></Link>
                <Link to="/skills" className="navbar-item">
                  <p className="title is-5">skills.</p>
                </Link>
                <Link to="/" className="navbar-item">
                  <i className="title is-5 fas fa-user-tie"></i>
                </Link>
                <Link to="/projects" className="navbar-item">
                  <p className="title is-5">projects.</p>
                </Link>
                <Link to="/contact" className="navbar-item">
                  <p className="title is-5">experience.</p>
                </Link>
              </div>
            </div>
          </div>
        </div>

      </nav>

    )
  }
}

export default withRouter(Navbar)
